import React from "react"
import { Helmet } from "react-helmet";
import { Link } from "react-router-dom";

export default class Furniture extends React.Component {
    render() {
        return (
            <div className='homepage center center-page-x'>
                <Helmet>
                    <meta charSet="utf-8" />
                    <title>Interactive 3d product visualization and AR for the furniture industry</title>
                    <link rel="canonical" href="https://www.ezyvue.com/industries/furniture" />
                </Helmet>
                <section className="full-width-viewer-header">
                    <div className="flexcol">
                        <div>
                            <h1>Realistic web-based 3D visualization and AR for the furniture industry</h1>
                            <h3>Create the highly engaging shopping experience that customers expect</h3>
                        </div>
                    </div>
                    <div>
                        <div className='homepage-viewer viewer-outer'>
                            <div className='aspect-inner'>
                                <iframe id="ezyvue-viewer"
                                    src={`${process.env.REACT_APP_VIEWER_URL}/viewer?productId=6370b157b531cd2aad5702b9&hidemenu`}
                                    width="100%"
                                    height="100%"
                                    style={{ border: 'none' }}
                                    frameBorder="0"
                                    allow="xr-spatial-tracking"
                                    allowFullScreen={true}
                                    mozallowfullscreen="true"
                                    webkitallowfullscreen="true" />
                            </div>
                        </div>
                    </div>    
                </section>
                <section className='homepage-section'>
                    <div className="flexrow">
                        <div className="flexcol">
                            <h2>Furniture personalization</h2>
                            <p>
                                With the product configurator shoppers can personalize an item and choose from the available colors, patterns, materials and features.
                            </p>
                            <Link to='/demos'>Try the furniture configurator demos</Link>
                        </div>
                        <div className="flexcol">
                            <img className='homepage-section-img'
                                alt='Augmented reality for iOS, Android and WebAR with product personalization' src={require('../../assets/images/sofa.jpg')} />
                        </div>
                    </div>
                </section>
                <section className='homepage-section'>
                    <div className="flexrow">
                        <div className='flexcol'>
                            <h2>Let consumers preview your products at home with AR</h2>
                            <p>
                                Ezyvue AR lets customers virtually place life-like models in their own room, helping them make the right choice.<br/>
                                App-less WebAR on Android also allows customers to try out a variety of color and material options.
                            </p>
                        </div>
                        <div className='flexcol'>
                            <div className="smartphone">
                                <div className="content">
                                    <img alt='augmented reality for ios and android' src={require('../../assets/images/ar.gif')} />
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className='flexcol'>
                        <h2>AR in the furniture retail industry</h2>
                        <p>
                            Furniture is currently the most popular product type that customers in the US want to see in AR.
                            According to a recent Camera IQ survey, &quot;76% of consumers have already used AR one way or another, and more than half of those who have not yet tried AR say they hope to do so in the future.&quot;
                            <br />
                            <br />
                        </p>
                        <div className="info-boxes-container">
                            <div className='info-box' style={{ backgroundColor: '#DDE3F3', fontSize: '16px' }}>
                                <div className='aspect-four-three'>
                                    <div className='aspect-inner'>
                                        <div className="info-box-outer">
                                            <div className="info-box-inner">
                                                Interacting with products that
                                                have AR experiences leads to a<br />
                                                <div style={{ fontSize: '80px' }}>94%</div>
                                                <b>higher conversion rate</b>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className='info-box' style={{ backgroundColor: '#DDE3F3', fontSize: '16px' }}>
                                <div className='aspect-four-three'>
                                    <div className='aspect-inner'>
                                        <div className="info-box-outer">
                                            <div className="info-box-inner">
                                                Brands are
                                                <div style={{ fontSize: '80px' }}>41%</div>
                                                <b>more likely to be considered if they have a branded AR experience</b>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className='info-box' style={{ backgroundColor: '#DDE3F3', fontSize: '16px' }}>
                                <div className='aspect-four-three'>
                                    <div className='aspect-inner'>
                                        <div className="info-box-outer">
                                            <div className="info-box-inner">
                                                There are<br />
                                                <div style={{ fontSize: '80px' }}>100</div>
                                                <b>million consumers shopping with AR online and in-stores.</b>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                <br />
                <br />
                <br />
            </div>
        );
    }
}